// src/components/DashboardLayout.tsx
"use client";

import { ReactNode, useState } from "react";
import ProtectedRoute from "./ProtectedRoute";
import Link from "next/link";
import Footer from "./Footer";

export default function DashboardLayout({ children }: { children: ReactNode }) {
  const [tab, setTab] = useState<"account"|"packages"|"history"|"reservar">("account");

  return (
    <ProtectedRoute>
      <div className="container py-4">
        {/* Top Navbar */}
        <nav className="d-flex justify-content-between align-items-center mb-4">
          <h2>Bloom Fisio</h2>
          <ul className="nav nav-pills">
            <li className="nav-item">
              <button
                className={`nav-link ${tab==="account"?"active":""}`}
                onClick={()=>setTab("account")}
              >Mi cuenta</button>
            </li>
            <li className="nav-item">
              <button
                className={`nav-link ${tab==="packages"?"active":""}`}
                onClick={()=>setTab("packages")}
              >Mis paquetes</button>
            </li>
            <li className="nav-item">
              <button
                className={`nav-link ${tab==="history"?"active":""}`}
                onClick={()=>setTab("history")}
              >Historial de reservas</button>
            </li>
            <li className="nav-item">
              <button
                className={`nav-link ${tab==="reservar"?"active":""}`}
                onClick={()=>setTab("reservar")}
              >Reservar</button>
            </li>
          </ul>
        </nav>

        {/* Content */}
        <div>
          {tab === "account" && <AccountSection />}
          {tab === "packages" && <PackagesSection />}
          {tab === "history" && <HistorySection />}
          {tab === "reservar" && <ReservasPage />}
        </div>
      </div>
      <Footer />
    </ProtectedRoute>
  );
}

// Importa o define estos componentes en l√≠nea:
import AccountSection from "../pages/dashboard/account";        // crea este
import PackagesSection from "../pages/dashboard/packages";     // tu packages.jsx
import HistorySection  from "../pages/dashboard/history";      // tu history.jsx
import ReservasPage    from "../pages/dashboard/reservas";     // tu reservas.jsx
